<template>
  <div class="container">
    <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
      <template #pulling><refreshLoading text="下拉刷新"/></template>
      <template #loosing><refreshLoading text="下拉刷新"/></template>
      <template #loading><refreshLoading text="加载中"/></template>
      <template #success><refreshLoading text="加载完成"/></template>
      <van-list :immediate-check="false" :finished="finished" v-model="loading" finished-text="没有更多啦~" @load="onLoad">
        <div class="prod-list">
          <div class="prod-item" v-for="item in vrList" :key="item" @click="toVrDetail(item)">
            <img :src="item.imgUrl.replace('http:', 'https:') | imageToWebp($webpSurpport)"/>
            <span>
              {{item.title}}
            </span>
            <div class="program-play"/>
          </div>
        </div>
      </van-list>
    </van-pull-refresh>
    <common-empty v-if="finished && vrList.length === 0"/>
  </div>
</template>
<script>
import { getVRPreviews } from '@/http/service/shop';
import refreshLoading from '@/components/refreshLoading'
const commonEmpty = () => import('../component/commonEmpty');
/**
 * VR 预览
 */
export default {
  components: {
    refreshLoading,
    commonEmpty,
  },
  data() {
    return {
      vrList: [],
      loading: false,
      finished: false,
      refreshing: false,
      pageNo: 1
    }
  },
  created() {
    this.getList();
  },
  methods: {
    onLoad() {

    },
    onRefresh() {
      this.getList()
    },
    getList() {
      getVRPreviews({ storeModelId: this.$route.query.storeModalId || '', pageModelId: this.$route.query.pageModalId || '' }).then(res => {
        if (res.success) {
          this.vrList = res.data || [];
        } else {
          this.$toast(res.message || '获取数据失败，请稍后重试');
        }
        this.loading = false;
        this.refreshing = false;
        this.finished = true;
      });
    },
    toVrDetail(item) {
      if (this.$util.isSgRn()) {
        window.postMessage(JSON.stringify({
          type: 'openOtherUrl',
          data: {
            newUrl: item.urlParam,
          }
        }));
      } else {
        this.$util.openNewWindow(item.urlParam, true);
      }
    }
  },
}
</script>
